.not-found-container {
  background-color: black;
  height: 85vh;
}

.not-found-image-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 70%;
}

.not-found-image {
  width: 30%;
  height: 80%;
}

.not-found-heading {
  color: white;
  text-align: center;
}

.not-found-para {
  color: white;
  text-align: center;
}

@media screen and (max-width: 767px) {
  .not-found-image {
    width: 100%;
    padding: 10px;
  }
}
